'use client'

import * as React from 'react';
import { useRouter, useSearchParams } from 'next/navigation';
import { signIn } from 'next-auth/react';
import { Button } from '../../components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '../../components/ui/card';
import { Input } from '../../components/ui/input';
import { Label } from '../../components/ui/label';
import { RadioGroup, RadioGroupItem } from '../../components/ui/radio-group';
import { toast } from 'sonner';
import { checkRateLimit, sanitizeInput } from '../../lib/security-utils';

// Test accounts for development - Match with database users
const TEST_ACCOUNTS = [
  { role: 'Admin', name: 'Admin User', email: 'admin@erp.local', password: 'password123' },
  { role: 'Manager', name: 'Sales Manager', email: 'sales@erp.local', password: 'password123' },
];

export function LoginPage() {
  const router = useRouter();
  const searchParams = useSearchParams();
  
  const [selectedAccountIndex, setSelectedAccountIndex] = React.useState(0);
  const [email, setEmail] = React.useState(TEST_ACCOUNTS[0].email);
  const [password, setPassword] = React.useState(TEST_ACCOUNTS[0].password);
  const [isLoading, setIsLoading] = React.useState(false);

  // Get callback URL from query params
  const callbackUrl = searchParams.get('callbackUrl') || '/dashboard';

  // Auto-fill when account selection changes
  React.useEffect(() => {
    const selectedAccount = TEST_ACCOUNTS[selectedAccountIndex];
    setEmail(selectedAccount.email);
    setPassword(selectedAccount.password);
  }, [selectedAccountIndex]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    // Rate limiting check
    const rateLimitKey = `login:${sanitizeInput(email)}`;
    const rateLimit = checkRateLimit(rateLimitKey, 5, 60000); // 5 attempts per minute
    
    if (!rateLimit.allowed) {
      const waitSeconds = Math.ceil((rateLimit.resetTime - Date.now()) / 1000);
      toast.error(`Quá nhiều lần thử. Vui lòng đợi ${waitSeconds} giây.`);
      return;
    }
    
    setIsLoading(true);

    try {
      // Use NextAuth signIn for proper session management
      console.log('Calling NextAuth signIn...');
      const result = await signIn('credentials', {
        email: sanitizeInput(email),
        password,
        redirect: false,
      });

      console.log('SignIn result:', result);

      if (result?.error) {
        toast.error('Email hoặc mật khẩu không đúng');
        setIsLoading(false);
        return;
      }

      if (result?.ok) {
        toast.success('Đăng nhập thành công!');
        
        // Redirect to callback URL or dashboard
        console.log('Redirecting to:', callbackUrl);
        router.push(callbackUrl);
        router.refresh();
      }
      
    } catch (error) {
      console.error('Login error:', error);
      toast.error('Đã xảy ra lỗi. Vui lòng thử lại.');
      setIsLoading(false);
    }
    // Removed finally block - don't reset loading on success (page redirects)
  };

  return (
    <div className="flex min-h-screen items-center justify-center bg-muted/40 p-4">
      <Card className="w-full max-w-md">
        <CardHeader>
          <CardTitle className="text-h3">Đăng nhập</CardTitle>
          <CardDescription>
            Chọn loại tài khoản để tự động điền thông tin đăng nhập
          </CardDescription>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit}>
            <div className="flex flex-col gap-6">
              {/* Account Selection */}
              <div className="grid gap-2">
                <Label>Loại tài khoản (Test)</Label>
                <RadioGroup 
                  value={selectedAccountIndex.toString()} 
                  onValueChange={(value) => setSelectedAccountIndex(parseInt(String(value)))}
                >
                  {TEST_ACCOUNTS.map((acc, index) => (
                    <div key={index} className="flex items-center space-x-2">
                      <RadioGroupItem value={index.toString()} id={`acc-${index}`} />
                      <Label htmlFor={`acc-${index}`} className="font-normal cursor-pointer">
                        {acc.role} - {acc.name} ({acc.email})
                      </Label>
                    </div>
                  ))}
                </RadioGroup>
              </div>

              {/* Email */}
              <div className="grid gap-2">
                <Label htmlFor="email">Email</Label>
                <Input
                  id="email"
                  type="email"
                  placeholder="m@example.com"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  required
                />
              </div>

              {/* Password */}
              <div className="grid gap-2">
                <div className="flex items-center">
                  <Label htmlFor="password">Mật khẩu</Label>
                  <a
                    href="#"
                    onClick={(e) => {
                      e.preventDefault();
                      toast.info('Tính năng quên mật khẩu đang phát triển');
                    }}
                    className="ml-auto inline-block text-sm underline-offset-4 hover:underline"
                  >
                    Quên mật khẩu?
                  </a>
                </div>
                <Input 
                  id="password" 
                  type="password" 
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required 
                />
              </div>

              {/* Submit Button */}
              <Button type="submit" className="w-full" disabled={isLoading}>
                {isLoading ? 'Đang đăng nhập...' : 'Đăng nhập'}
              </Button>

              {/* Google Login */}
              <Button 
                type="button"
                variant="outline" 
                className="w-full"
                onClick={() => toast.info('Tính năng đăng nhập với Google đang phát triển')}
              >
                Đăng nhập với Google
              </Button>
            </div>

            {/* Sign up link */}
            <div className="mt-4 text-center text-sm">
              Chưa có tài khoản?{' '}
              <a 
                href="#"
                onClick={(e) => {
                  e.preventDefault();
                  toast.info('Tính năng đăng ký đang phát triển');
                }}
                className="underline underline-offset-4 hover:text-primary"
              >
                Đăng ký ngay
              </a>
            </div>
          </form>
        </CardContent>
      </Card>
    </div>
  );
}
